.scrolling-div-nav{
    animation: move 25s infinite alternate-reverse;
}

@-webkit-keyframes move{
    form{
        transform: translateX(0px);
    }
    to{
        transform: translateX(-1100px);
    }
}

@keyframes move{
    form{
        transform: translateX(0px);
    }
    to{
        transform: translateX(-1100px);
    }
}
